<template>
  <div>
    <button open-type="getUserInfo" :getuserinfo="getuserinfoCallback">授权登录</button>
  </div>
</template>

<script>
export default {
  data() {
    return {};
  },
  onload() {
    wx.getSetting({
      success: function(res) {
        if (res.authSetting["scope.userInfo"]) {
          wx.getUserInfo({
            success: function(res) {
              console.log(res.userInfo);
              //用户已经授权过
            }
          });
        }
      }
    });
  },
  methods: {
    getuserinfoCallback(detail) {
      console.log(detail);
    }
  }
};
</script>


